import {
  AUTORIZATION_LOGIN__LOADING,
  AUTORIZATION_LOGIN__SUCCESS,
  AUTORIZATION_LOGIN__ERROR,
  LOGOUT_LOGIN
} from 'actions/login'
import { Map } from 'immutable'


const initialState = Map({
  login: '',
  token: '',
  loading: false,
  error: false
});


export default function login(state = initialState, action) {
  switch (action.type) {
    case AUTORIZATION_LOGIN__LOADING:
      return state.merge(
        Map({
          loading: true,
          error: false
        })
      );

    case AUTORIZATION_LOGIN__SUCCESS:
      return state.merge(
        Map({
          login: action.login,
          token: action.token,
          loading: false,
          error: false
        })
      );

    case AUTORIZATION_LOGIN__ERROR:
      return state.merge(
        Map({
          loading: false,
          error: true
        })
      );


    case LOGOUT_LOGIN:
      return state.merge(
        Map({
          login: '',
          token: '',
          loading: false,
          error: false
        })
      );

    default:
      return state;
  }
}




