import axios from 'axios'


export const AUTORIZATION_LOGIN__LOADING = 'AUTORIZATION_LOGIN__LOADING'
export const AUTORIZATION_LOGIN__SUCCESS = 'AUTORIZATION_LOGIN__SUCCESS'
export const AUTORIZATION_LOGIN__ERROR = 'AUTORIZATION_LOGIN__ERROR'
export const LOGOUT_LOGIN = 'LOGOUT_LOGIN'




export const onCurrentAccount = () => async dispatch => {

  const loginLS = localStorage.getItem('login');
  const tokenLS = localStorage.getItem('token');

  if (loginLS && tokenLS) {
    dispatch({
      type: AUTORIZATION_LOGIN__SUCCESS,
      login: loginLS,
      token: tokenLS
    })
  }
}



export const onAuthentication = ({ login, password }, callback) => async dispatch => {
  dispatch({
    type: AUTORIZATION_LOGIN__LOADING
  })

  try {
    const token = await axios.post('/api/authentication', {
      login,
      password
    }).catch(error => { throw error })

    localStorage.setItem('login', login)
    localStorage.setItem('token', token.data)

    dispatch({
      type: AUTORIZATION_LOGIN__SUCCESS,
      login,
      token: token.data
    })


    callback(true)
  } catch (error) {
    dispatch({
      type: AUTORIZATION_LOGIN__ERROR
    })

    callback(false)
  }
}




export const onLogoutLogin = () => dispatch => {
  dispatch({
    type: LOGOUT_LOGIN
  })

  localStorage.removeItem('login');
  localStorage.removeItem('token');
}




