import { axiosSetToken } from '../../config/axiosConfig'

import { AUTORIZATION_LOGIN__SUCCESS } from 'actions/login'


export default store => next => action => {

  if (action.type === AUTORIZATION_LOGIN__SUCCESS) axiosSetToken(action.token)

  return next(action)
}


